import React, { useRef, useEffect } from 'react';
import _throttle from 'lodash/throttle';
import styles from './_scroll.module.scss';

export const Scroll = () => {
  const container = useRef(null);
  const container2 = useRef(null);
  const container3 = useRef(null);
  const prevDate = useRef(0);

  const onWheel = (e) => {
    e.preventDefault();
    console.log(e.deltaX, e.deltaY, e.wheelDelta);
  };

  const onWheel2 = (e) => {
    e.preventDefault();
    console.log(Date.now() - prevDate.current, e.wheelDelta);
    prevDate.current = Date.now()
  };

  useEffect(() => {
    container.current.addEventListener('wheel', onWheel);
    container2.current.addEventListener('wheel', _throttle(onWheel, 0, { leading: true }));
    container3.current.addEventListener('wheel', onWheel2);
  }, []);

  return (
    <div className={styles.root}>
      <div ref={container3} className={styles.scrollBox}>
        scroll me
      </div>
      <div ref={container} className={styles.scrollBox}>
        scroll me
      </div>
      <div ref={container2} className={styles.scrollBox}>
        scroll me <br />
        <span>(with throttle)</span>
      </div>
    </div>
  );
};
